import Header from '../../components/Header'
import {queryOpenedDetail, queryClosedDetail} from '../../api/API'
import ScreenUtil from '../../utils/ScreenUtil'
import MathUtil from '../../utils/MathUtil'
export default {
  name: 'ClosedDetailContent',
  components: {
    Header
  },
  data () {
    return {
      contentData: {
        buy_commission: 0,
        service_fee: 0,
        buy_securities_fee: 0,
        sell_commission: 0,
        sell_securities_fee: 0
      },
      getBuyFee () {
        return MathUtil.add(this.contentData.buy_securities_fee, MathUtil.add(this.contentData.buy_commission, this.contentData.service_fee)).toFixed(2)
      },
      getSellFee () {
        return MathUtil.add(this.contentData.sell_commission, this.contentData.sell_securities_fee).toFixed(2)
      },
      clientSize: ScreenUtil.getScreenSize()
    }
  },
  mounted () {
    this.$on('loadData', this.loadData)
  },
  beforeDestroy () {
    this.$off('loadData', this.loadData)
    this.contentData = {
      buy_commission: 0,
      service_fee: 0,
      buy_securities_fee: 0,
      sell_commission: 0,
      sell_securities_fee: 0
    }
  },
  methods: {
    async loadData (id, type) {
      this.clientSize = ScreenUtil.getScreenSize()
      this.contentData = {}
      let res
      if (type === 0) {
        res = await queryOpenedDetail(this.$store.state.User.token, id)
      } else {
        res = await queryClosedDetail(this.$store.state.User.token, id)
      }
      if (res.code === 200) {
        this.contentData = res.data
      }
    },
    onBackClicked () {
      this.$emit('close')
    }
  }
}



